body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center{
    position: fixed;
    left: 50%; 
    top: 50%; 
    -webkit-transform: translate(-50%, -50%); 
            transform: translate(-50%, -50%); 
}
.input{
    width: 400px;
}
.btn_holder{
    width: 200px;
    position: fixed;
    left: 50%; 
    top:130px;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%)
}
.message{
    max-width: 100px;
    overflow-wrap: break-word;
    background: red;
}
.button {
    background: linear-gradient(45deg, #fe6b8b 30%, #ff8e53 90%);
    border-radius: 3px;
    border: 0;
    color: white;
    height: 48px;
    padding: 0 30px;
    box-shadow: 0 3px 5px 2px rgba(255, 105, 135, .3);
  }
  .btn_send_holder{
      position: fixed;
      bottom: 100px;
      left: 520px;
  }
  .input2_holder{
      position: fixed;
      left: 50px;
      top: 45px;
      width: 450px;
      height: 50px;
      border-radius: 200px;
      box-shadow: 1px 3px 10px darkgray;
      background: lightcyan;
      display: block;
  }
  .input2{
    /* top: 10px; */
    left: 60px; 
    width:350px;
    overflow-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
  }
  .chat_background{
      position: fixed;
      top: 0px;
      left: 50%;
      -webkit-transform: translate(-50%, 0%);
              transform: translate(-50%, 0%);
      width: 600px;
      height: 100%;
      background: honeydew;
  }
  .Your_message{
    position: relative;
    right: 10px;
    padding-top: 10px;
  }
  .Partner_message{
    position: relative;
    left: 10px;
    padding-top: 10px;
  }
  .bar_and_button{
      position: fixed;
      top: 550px;
      left: 50%;
      -webkit-transform: translate(-50%, 0%);
              transform: translate(-50%, 0%);
      height: 200px;
      width: 600px;
  }
  .all_messages{
      position: fixed;
      top: 60px;
      left: 50%;
      -webkit-transform: translate(-50%, 0%);
              transform: translate(-50%, 0%);
      width: 600px;
      height: 540px;
      overflow: auto;
      overflow-x: hidden;
  }
.exit_button_holder{
    position: fixed;
    top:50%;
    left: 800px;
}
.exiting_notification{
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
}
.Info{
    max-width: 400px;
    left: 40px;
    position: fixed;
    top: 50%;
    -webkit-transform: translate(0%, -50%);
            transform: translate(0%, -50%);
}
